    padding: 8px 10px;
    height: 32px;
    line-height: 1em;
    border-radius: 2px;
    background: #fff;
    color: var(--accent-color);
    cursor: pointer;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.12);
    font-weight: 600;
    font-size: 14px;
    white-space: pre;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
